<template>
    <form v-on:submit.prevent="onSubmit" class="search-form">
        <input
            v-model="searchQuery"
            class="search-form__input"
            type="text"
            placeholder="Search Youtube"
        >
        <input
            class="search-form__submit"
            type="submit"
            value=""
        >
    </form>
</template>

<script>
export default {
    data: () => ({
        searchQuery: ''
    }),
    methods: {
        onSubmit() {
            const searchQuery = this.searchQuery.split(' ').join('+');

            this.$store.dispatch('updateSearchQuery', searchQuery);
            this.$store.dispatch('updatePageToken', '');
            this.$store.dispatch('fetchSearchResults');

            if (this.$router.currentRoute.name != 'home') {
                this.$router.push({ name: 'home' });
            }
        }
    }
}
</script>

<style lang="scss">
.search-form {
    display: block;

    &__input {
        height: 38px;
        font-size: 16px;
        padding: 3px 10px;
        border: 1px solid rgba(#282828, 0.3);
        outline: 0;
        box-shadow: none;
        color: #282828;
        vertical-align: middle;
    }

    &__submit {
        height: 38px;
        width: 38px;
        vertical-align: middle;
        border: 1px solid rgba(#282828, 0.3);
        border-left: 0;
        background-color: rgba(#282828, 0.05);
        transform: translateX(-1px);
        background-position: center;
        background-repeat: no-repeat;
        background-size: 60%;
        background-image: url(data:image/svg+xml;utf8;base64,);
        transition: .1s background-color ease-in-out;

        &:hover {
