<template>
    <div class="pagination-nav">
        <button
            class="pagination-nav__btn pagination-nav__btn--prev"
            :class="disabledPrevClass"
            @click="onPageChange(prevPageToken)"
        ></button>
        <button
            class="pagination-nav__btn pagination-nav__btn--next"
            :class="disabledNextClass"
            @click="onPageChange(nextPageToken)"
        ></button>
    </div>
</template>

<script>
export default {
    props: {
        prevPageToken: {
            type: String,
            required: false
        },
        nextPageToken: {
            type: String,
            required: false
        }
    },
    methods: {
        onPageChange(pageToken) {
            if (pageToken) {
                this.$store.dispatch('updatePageToken', pageToken);
                this.$store.dispatch('fetchSearchResults');
            }
        }
    },
    computed: {
        disabledPrevClass() {
            if (!this.prevPageToken) {
                return 'pagination-nav__btn--disabled';
            }
        },
        disabledNextClass() {
            if (!this.nextPageToken) {
                return 'pagination-nav__btn--disabled';
            }
        }
    }
}
</script>

<style lang="scss">
.pagination-nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    &__btn {
        display: block;
        width: 40px;
        height: 40px;
        background-color: rgba(#282828, 0.05);
        border: 1px solid rgba(#282828, 0.3);
        transition: .1s background-color ease-in-out;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 60%;
        background-image: url(data:image/svg+xml;utf8;base64,);

        &:hover {
            cursor: pointer;
            background-color: rgba(#282828, 0.08);
        }

        &--prev {
            transform: rotate(180deg);
        }

        &--disabled {
            opacity: 0.3;

            &:hover {
                cursor: not-allowed;
                background-color: rgba(#282828, 0.05);
            }
        }
    }
}
</style>
